//  
export const PostDelete = async (auth) => {
  const url = window.location.pathname;
  // &&  id  username  (temp:admin)
  if (auth) {
    var bool = window.confirm(" ?");
    // 
    if (!bool) {
      return;
    }
    //
    const request = await fetch("https://find-thanku.herokuapp.com" + url, {
      method: "DELETE",
    });
    if (!request.ok) {
      alert("  ");
      return;
    }
    await request.json();
    alert("!");
  }
  //!=  ->  
  else {
    alert("  ");
  }
};

//  
export const PostEdit = async (auth, content) => {
  const url = window.location.pathname;
  // ==  id (admin)
  if (auth) {
    var newcontent = "content"; //upload   ? find_edit view   newcont 
    const request = await fetch(
      "https://find-thanku.herokuapp.com" + url + "/content/" + newcontent,
      {
        method: "PATCH",
      }
    );
    if (!request.ok) {
      alert("  ");
      return;
    }
    await request.json();
    alert("!");
    window.location.reload(true); //
  }
  //!=  ->  
  else {
    alert("  ");
  }
};
