// 
const SetReplynum = async (num) => {
  const url = window.location.pathname;
  const request = await fetch(
    "https://find-thanku.herokuapp.com" + url + "/replynum/" + num,
    {
      method: "PATCH",
    }
  );
  console.log("https://find-thanku.herokuapp.com" + url + "/replynum/" + -1);
  if (!request.ok) {
    alert(" ");
    return;
  }
  await request.json();
};

export const PostDelete = async (delauth, id) => {
  const url = window.location.pathname;
  // &&  ==   googleId
  if (delauth) {
    var bool = window.confirm(" ?");
    // 
    if (!bool) {
      return;
    }
    //
    const request = await fetch(
      "https://find-thanku.herokuapp.com" + url + "/comment/" + id,
      {
        method: "DELETE",
      }
    );
    if (!request.ok) {
      alert("  ");
      return;
    }
    await request.json();
    SetReplynum(-1); // -1
    alert(" !");
    window.location.reload(true); //
  }
  //!=  ->  
  else {
    alert("  ");
  }
};
// 
export const PostEdit = async (editauth, content, id) => {
  const url = window.location.pathname;
  // ==   googleId
  if (editauth) {
    var input = prompt("  ", content);
    if (!input) {
      input = content; //input==null( )
    }
    const request = await fetch(
      "https://find-thanku.herokuapp.com" +
        url +
        "/comment/" +
        id +
        "/content/" +
        input,
      {
        method: "PATCH",
      }
    );
    if (!request.ok) {
      alert("  ");
      return;
    }
    await request.json();
    window.location.reload(true); //
  }
  //!=  ->  
  else {
    alert("  ");
  }
};
