import React from "react";

import * as S from "./styles";

const FindBoardHeader = () => {
  return (
    <S.BoardHeader>
      <S.TitleContainer>
        <S.Label></S.Label>
      </S.TitleContainer>
      <S.NameContainer>
        <S.Label> </S.Label>
      </S.NameContainer>
      <S.LostPlaceContainer>
        <S.Label> </S.Label>
      </S.LostPlaceContainer>
      <S.FindPlaceContainer>
        <S.Label> </S.Label>
      </S.FindPlaceContainer>
      <S.CommentContainer>
        <S.Label> </S.Label>
      </S.CommentContainer>
      <S.WriterContainer>
        <S.Label></S.Label>
      </S.WriterContainer>
      <S.DateContainer>
        <S.Label> </S.Label>
      </S.DateContainer>
    </S.BoardHeader>
  );
};

export default FindBoardHeader;
