// 
export const PostDelete = async (delauth) => {
  const url = window.location.pathname; //localhost:4000/lost_post/ id/
  // &&  ==   username (temp:nonamed)
  if (delauth) {
    var bool = window.confirm(" ?");
    // 
    if (!bool) {
      return;
    }
    //
    const request = await fetch("https://find-thanku.herokuapp.com" + url, {
      method: "DELETE",
    });
    if (!request.ok) {
      alert("  ");
      return;
    }
    await request.json();
    alert("!");
  }
  //!=  ->  
  else {
    alert("  ");
  }
};

// 
export const SetReplynum = async (num) => {
  const url = window.location.pathname;
  const request = await fetch(
    "https://find-thanku.herokuapp.com" + url + "/replynum/" + num,
    {
      method: "PATCH",
    }
  );
  console.log("https://find-thanku.herokuapp.com" + url + "/replynum/" + 1);
  if (!request.ok) {
    alert(" ");
    return;
  }
  await request.json();
};
