import React from "react";

import * as S from "./styles";
import Layout from "../../component/layout";
import FindBoardHeader from "../../container/find-board-header";
import LostBoardHeader from "../../container/lost-board-header";

import FindBoardList from "../../container/find-board-list";
import LostBoardList from "../../container/lost-board-list";

import { useMyPage } from "./hooks"; //
import { useMyPage2 } from "./hooks2"; //
import { storage } from "../google_login/storage";

const MyPage = () => {
  //    ...
  const googleId = storage.get("loggedInfo").googleId; // 
  const imageurl = storage.get("loggedInfo").imgUrl;
  const username = storage.get("loggedInfo").username; //ex: [/]
  const email = storage.get("loggedInfo").email; //ex: wjdthgmlgo@korea.ac.kr
  const emailname = email.split("@")[0]; //ex: wjdthgmlgo
  console.log(emailname);

  //  posts 
  const { posts } = useMyPage(googleId); // post
  const { posts2 } = useMyPage2(googleId); // post

  // console.log("posts", posts);
  console.log("posts2", posts2);

  return (
    <Layout>
      <S.MyPage>
        <S.MyInfoContainer>
          <S.Title> </S.Title>
          <S.MyInfo>
            <S.ImageContainer>
              <img src={imageurl} alt="profile" />
            </S.ImageContainer>
            <S.MetaContainer>
              <S.Meta>: {username}</S.Meta>
              <S.Meta>: {emailname}</S.Meta>
              <S.Meta>: {email}</S.Meta>
            </S.MetaContainer>
          </S.MyInfo>
        </S.MyInfoContainer>

        <S.Title>  </S.Title>
        <S.Container>
          <S.Label> </S.Label>
          <S.BoardListContainer>
            <LostBoardHeader />
            <LostBoardList posts={posts} />
          </S.BoardListContainer>
        </S.Container>
        <S.Container>
          <S.Label> </S.Label>
          <S.BoardListContainer>
            <FindBoardHeader />
            <FindBoardList posts={posts2} />
          </S.BoardListContainer>
        </S.Container>
      </S.MyPage>
    </Layout>
  );
};

export default MyPage;
