//   comment  
//url= localhost:4000/find_post/ id/comment
import { useEffect, useState } from "react";

export const useCommentFind = () => {
  const [comments, setComments] = useState([]);

  useEffect(() => {
    const getInitialComments = async () => {
      const url = window.location.pathname; //localhost:4000/find_post/ id/

      console.log("https://find-thanku.herokuapp.com" + url + "/comment");
      const request = await fetch(
        "https://find-thanku.herokuapp.com" + url + "/comment",
        {
          method: "GET",
        }
      );

      if (!request.ok) {
        alert(" ");
        return;
      }

      const data = await request.json();

      setComments(data);
      console.log(data);
    };

    getInitialComments();
  }, []);

  return {
    comments,
  };
};
